.patronite {
    display: flex;
    align-items: center;
    justify-content: center;
     gap: 13px;
    width: 342px;
    height: 66px;
    border-bottom: 0.75px solid rgba(255, 255, 255, 0.7);
    border-top: 0.75px solid rgba(255, 255, 255, 0.7);
    font-size: 16px;
}

.patronite__email {
    font-weight: 600;
    color: rgba(255, 255, 255, 0.8);
}

@media only screen and (min-width: 833px) {
    .patronite{
        width: 460px;
        height: 66px;
        margin: 36px 0;
    }
}